
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
}

.hrd-box {
    position: relative;
    width: 402px;
    height: 502px;
    background: #ccc;
    border: 2px solid #666
}
.hrd-box .checker.on {
    z-index: 22
}
.hrd-box .checker {
    position: absolute;
    background: rgb(122, 50, 2);
    border: 1px solid #ff0;
    text-align: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    color: #fff;
    -webkit-transition: top 0.6s, left 0.6s;
    transition: top 0.6s, left 0.6s;
}
.hrd-box .checker .btns {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.hrd-box .checker span {
    display: none;
    border: #ff0 100% solid;
    border-radius: 50%;
    -webkit-transition: opacity 0.6s;
    transition: opacity 0.6s;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    position: absolute;
    background: rgb(230, 200, 152);
    width: 100%;
    height: 50px;
    cursor: pointer;
}
.hrd-box .checker .showtop span.top {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.hrd-box .checker .showbottom span.bottom {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.hrd-box .checker .showleft span.left {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.hrd-box .checker .showright span.right {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.hrd-box .checker span.top {
    top: -50px;
}
.hrd-box .checker span.bottom {
    bottom: -50px;
}
.hrd-box .checker span.left {
    left: -50px;
    height: 100%;
    top: 0;
    width: 50px;
}
.hrd-box .checker span.right {
    right: -50px;
    top: 0;
    height: 100%;
    width: 50px;
}
